import React from "react";
import AdminThemeItem from "../AdminThemeItem/AdminThemeItem";
import ThemeCount from "../ThemeCount/ThemeCount";

function AdminDashboard({ array, setArray, setLogged }) {
  const [themes, setThemes] = React.useState(array);
  const [currentBoard, setCurrentBoard] = React.useState([]);
  const [currentSub, setCurrentSub] = React.useState([]);
  const [isLoading, setLoading] = React.useState(false);
  // React.useEffect(() => {
  //   return () => {
  //     const data = JSON.parse()
  //     setThemes(JSON.parse([]
  //     ))
  //   }
  // }, [])
  
  // React.useEffect(() => {
  //   setThemes(array);
  //   setLoading(false);
  // }, []);
  function createTheme() {
    // themes.unshift();
    setThemes((prev) => [
      {
        id: Date.now().toString(),
        name: null,
        backgroundImage: null,
        subtopics: [],
      },
      ...prev,
    ]);
  }
  function compareArrays(a1, a2) {
     if(JSON.stringify(a1) === JSON.stringify(a2)) {
       return true
     
      }
     else return false
  }
    
    
  function saveAllChanges(arr) {
    const json = JSON.stringify(arr);
    try {
      fetch("/newarray", {
        method: "POST",
        body: json,
        headers: {
          "Content-Type": "application/json;charset=utf-8",
        },
      })
        .then((data) => data.json())
        .then((arr) => setArray(arr));
    } catch (error) {
      console.error(error);
    }
  }
  function exitHandler() {
    if (compareArrays(array, themes)) {
      const exit = window.confirm(
        "–í—ã —É–≤–µ—Ä–µ–Ω—ã —á—Ç–æ —Ö–æ—Ç–∏—Ç–µ –≤—ã–π—Ç–∏? –í—Å–µ –Ω–µ—Å–æ—Ö—Ä–∞–Ω—ë–Ω–Ω—ã–µ –∏–∑–º–µ–Ω–µ–Ω–∏—è –Ω–µ –±—É–¥—É—Ç –ø—Ä–∏–º–µ–Ω–µ–Ω—ã." ===
          true)
      if (exit)
      setLogged(false);
      else return
    }
    else setLogged(false);
  }
  return (
    <div className="dashboard-container" onLoad={() => setLoading(false)}>
      <div className="toolbar">
        <button onClick={() => exitHandler()}>–í—ã–π—Ç–∏</button>
        <button onClick={() => createTheme()}>–î–æ–±–∞–≤–∏—Ç—å —Ç–µ–º—É</button>
        <button
          onClick={() => saveAllChanges(themes)}
        >
          –°–æ—Ö—Ä–∞–Ω–∏—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏—è
        </button>
        <ThemeCount themes={themes} />
      </div>
      {!isLoading && (
        <div className="themes-container">
          {themes.map((theme) => (
            <AdminThemeItem
              key={theme.id}
              theme={theme}
              themes={themes}
              setThemes={setThemes}
              currentBoard={currentBoard}
              setCurrentBoard={setCurrentBoard}
              currentSub={currentSub}
              setCurrentSub={setCurrentSub}
              setArray={setThemes}
              // saveAllChanges={saveAllChanges}
            />
          ))}
        </div>
      )}
    </div>
  );
}

export default AdminDashboard;
