import React from "react";

function ThemeCount({ themes }) {
  const [countSubs, setCountSubs] = React.useState(
    themes.reduce(
      (previousValue, currentValue) =>
        (previousValue += currentValue.subtopics.length),
      0
    )
  );
  const [countContent, setCountContent] = React.useState(
    themes.reduce(
      (previousValue, currentValue) =>
        (previousValue += currentValue.subtopics.reduce((previousValue, currentValue) =>
        (previousValue += currentValue.content.length),
      0)),
      0
    )
  );
  // setCountSubs(themes.reduce((previousValue = 0, currentValue) => {
  //   return previousValue + currentValue.subtopics.length
  // }))
  // for (let index = 0, count = 0; index < themes.length; index++) {
  //   count = count + themes[index].subtopics.length;
  //   if(index === themes.length-1) setCountSubs(count)
  // }
  // const count
  return (
    <div className="count-contents">
      –í—Å–µ–≥–æ: <span className="th">{themes.length} —Ç–µ–º</span> <span className="sub">{countSubs} –ø–æ–¥—Ç–µ–º</span> <span className="cont">{countContent} –∫–∞—Ä—Ç–æ—á–µ–∫ –ø–æ–¥—Ç–µ–º</span>
    </div>
  );
}

export default ThemeCount;
