import React from 'react';
import ChangeableContent from "../ChangeableContent/ChangeableContent";
function ChangeableContentContainer({currentData, sub, setCurrentData}) {
  const [currentContent, setCurrentContent] = React.useState(null);
  return <div className="content-container">
  {currentData.content.map((content, index) => (
    <ChangeableContent
      key={content.img || content.text || Date.now()}
      content={content}
      index={index}
      setCurrentData={setCurrentData}
      sub={sub}
      currentContent={currentContent}
      setCurrentContent={setCurrentContent}
    />
  ))}
</div>;
}

export default ChangeableContentContainer;
