import "./App.css";
import "./AdminStyles.css";
import React from "react";
import AdminDashboard from "./components/AdminDashboard/AdminDashboard";
import BaseComp from "./components/BaseComp/BaseComp";
function App() {
  // init states
  const [array, setArray] = React.useState([]);

  const [isLoading, setLoad] = React.useState(true);
  const [isLogged, setLogged] = React.useState(false);

  // loading themes and update states
  React.useEffect(() => {
    (async () => {
      try {
        let themes = await fetch("http://localhost:3000/arr").then((data) => data.json()).catch((err) => {console.error(err)});
        if (themes !== undefined) {
          setArray(themes);  
        }
        else {
          themes = await fetch("/arr").then((data) => data.json());
          setArray(themes);
        }
        
      } finally {
        setLoad(false);
      }
    })();
  }, []);

  return (
    <React.Fragment>
      {!isLoading ? (
        !isLogged ? (
          <div
            className="App"
            onLoad={() => {
              window.addEventListener("load", () => setLoad(false));
            }}
          >
            <BaseComp
              isLoading={isLoading}
              setLoad={setLoad}
              themes={array}
              // setThemes={setArray}
              setLogged={setLogged}
            />
          </div>
        ) : (
          <AdminDashboard array={array} setArray={setArray} setLogged={setLogged}/>
        )
      ) : (
        <img
          id="loader"
          src={"img/480px-Loader.gif"}
          alt={isLoading.toString()}
        />
      )}
    </React.Fragment>
  );
}

export default App;
