import React from "react";
import AdminSubItem from "../AdminSubItem/AdminSubItem";
import ChangeableImage from "../ChangeableImage/ChangeableImage";

function AdminThemeItem({
  theme,
  themes,
  setThemes,
  currentBoard,
  setCurrentBoard,
  currentSub,
  setCurrentSub,
  setArray,
  saveAllChanges,
}) {
  const [currentTheme, setCurrentTheme] = React.useState(theme);
  const [isChangeable, setChangeable] = React.useState(false);
  const [isActive, setActive] = React.useState(false);

  function dragOverHandler(e) {
    e.preventDefault();
    e.stopPropagation();
    if (
      (e.currentTarget.className === "sub-item" &&
        currentSub.subtopics === undefined) ||
      (e.currentTarget.className === "theme-item" &&
        currentSub.subtopics !== undefined)
    )
      e.currentTarget.style.boxShadow = "0 4px 3px gray";
  }

  function dragLeaveHandler(e) {
    if (
      (e.currentTarget.className === "sub-item" &&
        currentSub.subtopics === undefined) ||
      (e.currentTarget.className === "theme-item" &&
        currentSub.subtopics !== undefined)
    )
      e.currentTarget.style.boxShadow = "2px 1px 2px rgb(230, 227, 227)";
  }

  function dragStartHandler(e, theme, sub) {
    e.stopPropagation();
    if (
      e.target.className === "sub-item" ||
      e.target.className === "theme-item"
    ) {
      setCurrentBoard(theme);
      setCurrentSub(sub);
    }
  }

  function dragEndHandler(e) {
    e.currentTarget.style.boxShadow = "2px 1px 2px rgb(230, 227, 227)";
  }

  function dropHandler(e, theme, sub) {
    if (
      e.currentTarget.className === "sub-item" ||
      e.currentTarget.className === "theme-item"
    )
      e.currentTarget.style.boxShadow = "2px 1px 2px rgb(230, 227, 227)";
    e.stopPropagation();
    e.preventDefault();
    if (
      currentSub !== undefined &&
      currentSub.subtopics === undefined &&
      e.currentTarget.className === "sub-item"
    ) {
      const currentIndex = currentBoard.subtopics.indexOf(currentSub);
      currentBoard.subtopics.splice(currentIndex, 1);
      const dropIndex = theme.subtopics.indexOf(sub);
      dropIndex < currentIndex
        ? theme.subtopics.splice(dropIndex, 0, currentSub)
        : theme.subtopics.splice(dropIndex + 1, 0, currentSub);
      setThemes(
        themes.map((b) => {
          if (b.id === theme.id) {
            return theme;
          }
          if (b.id === currentBoard.id) {
            return currentBoard;
          }
          return b;
        })
      );
    } else if (
      currentSub.subtopics !== undefined &&
      e.currentTarget.className === "theme-item"
    ) {
      const currentIndex = currentBoard.indexOf(currentSub);
      currentBoard.splice(currentIndex, 1);
      const dropIndex = theme.indexOf(sub);
      dropIndex < currentIndex
        ? theme.splice(dropIndex, 0, currentSub)
        : theme.splice(dropIndex + 1, 0, currentSub);
      setThemes(
        themes.map((b) => {
          if (b.id === theme.id) {
            return theme;
          }
          if (b.id === currentBoard.id) {
            return currentBoard;
          }
          return b;
        })
      );
    }
  }

  function dropCardHandler(e, theme) {
    theme.subtopics.push(currentSub);
    const currentIndex = currentBoard.subtopics.indexOf(currentSub);
    currentBoard.subtopics.splice(currentIndex, 1);
    setThemes(
      themes.map((b) => {
        if (b.id === theme.id) {
          return theme;
        }
        if (b.id === currentBoard.id) {
          return currentBoard;
        }
        return b;
      })
    );
  }

  function createSubItem(theme) {
    theme.subtopics.push({
      id: Date.now(),
      name: null,
      backgroundImage: null,
      img: null,
      text: null,
      content: [],
      map: null,
    });
    setThemes((prev) => prev.map((t) => (t.id === theme.id ? theme : t)));
  }
  function deleteTheme(theme) {
    const confirmDel = window.confirm(
      "–í—ã –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å —ç—Ç—É —Ç–µ–º—É?"
    );
    if (confirmDel === true) {
      setThemes((prev) => prev.filter((t) => t.id !== theme.id));
      fetch("/deltheme", {
        method: "POST",
        body: JSON.stringify({
          id: theme.id,
          array: themes.filter((t) => t.id !== theme.id),
        }),
        headers: {
          "Content-Type": "application/json",
        },
      })
        .then((result) => {
          console.log(result);
        })
        // .then((response) => response.json())
        // .then((arr) => {
        //   setArray(arr);
        //   setThemes(arr);
        // })
        .catch((err) => {
          console.log(err);
        });
    }
  }
  function saveChanges(currentTheme) {
    setThemes((prev) =>
      prev.map((t) => (t.id === theme.id ? currentTheme : t))
    );
    setChangeable(false);
    // setCurrentTheme(theme);
  }
  function undoChanges() {
    setChangeable(false);
    setCurrentTheme(theme);
  }

  function deleteThemeImage(e, path) {
    // e.preventDefault();
    // e.stopPropagation();

    const confirmDel = window.confirm(
      "–í—ã —É–≤–µ—Ä–µ–Ω—ã —á—Ç–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å –∏–∑–æ–±—Ä–∞–¥–µ–Ω–∏–µ? –ü—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ –¥–∞–Ω–Ω—ã–µ —ç—Ç–æ–≥–æ –±–ª–æ–∫–∞ –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ —Å–æ—Ö—Ä–∞–Ω—è—Ç—Å—è"
    );
    if (confirmDel === true) {
      try {
        fetch("/delimage", {
          method: "POST",
          body: JSON.stringify({ img: path }),
          headers: {
            "Content-Type": "application/json",
          },
        })
          .then(() => {
            setThemes((prev) =>
              prev.map((t) =>
                t.id === theme.id ? { ...theme, backgroundImage: null } : t
              )
            );
            // saveAllChanges(
            //   themes.map((t) =>
            //     t.id === theme.id ? { ...theme, backgroundImage: null } : t
            //   )
            // );
          })
          .catch((err) => {
            setThemes((prev) =>
              prev.map((t) =>
                t.id === theme.id ? { ...theme, backgroundImage: null } : t
              )
            );
            // saveAllChanges(
            //   themes.map((t) =>
            //     t.id === theme.id ? { ...theme, backgroundImage: null } : t
            //   )
            // );
            console.error(err);
          });
      } catch (error) {
        console.error(error);
      }
    }
  }

  return (
    <div
      onDragLeave={(e) => dragLeaveHandler(e)}
      onDragStart={(e) => dragStartHandler(e, themes, theme)}
      onDragEnd={(e) => dragEndHandler(e)}
      draggable="true"
      onDragOver={(e) => dragOverHandler(e)}
      onDrop={(e) =>
        currentSub.subtopics === undefined
          ? dropCardHandler(e, theme)
          : dropHandler(e, themes, theme)
      }
      className="theme-item"
    >
      <div className="toolbar">
        {!isChangeable ? (
          <>
            {!isActive ? (
              <button onClick={() => setActive(true)}>–ü–æ–¥—Ä–æ–±–Ω–µ–µ</button>
            ) : (
              <button onClick={() => setActive(false)}>–°–∫—Ä—ã—Ç—å</button>
            )}

            <button onClick={() => setChangeable(true)}>–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å</button>
            <button onClick={() => deleteTheme(theme)}>–£–¥–∞–ª–∏—Ç—å</button>
            <button onClick={() => createSubItem(theme)}>
              –î–æ–±–∞–≤–∏—Ç—å –ø–æ–¥—Ç–µ–º—É
            </button>
          </>
        ) : (
          <>
            <button onClick={() => undoChanges()}>–û—Ç–º–µ–Ω–∏—Ç—å</button>
            <button onClick={() => deleteTheme(theme)}>–£–¥–∞–ª–∏—Ç—å</button>
            <button onClick={() => saveChanges(currentTheme)}>–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</button>
          </>
        )}
      </div>
      {!isChangeable ? (
        isActive ? (
          <>
            <label>–ù–∞–∑–≤–∞–Ω–∏–µ —Ç–µ–º—ã</label>
            <h2>{theme.name}</h2>
            <label>–§–æ–Ω–æ–≤–æ–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Ç–µ–º—ã</label>
            <div className="img-container">
              <img
                width="80"
                height="70"
                src={theme.backgroundImage}
                alt="–ò–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Ç–µ–º—ã"
              />
            </div>
          </>
        ) : (
          <h2>{theme.name}</h2>
        )
      ) : (
        <>
          <label>–ù–∞–∑–≤–∞–Ω–∏–µ —Ç–µ–º—ã</label>
          <input
            type="text"
            value={currentTheme.name || ""}
            onChange={(e) => {
              setCurrentTheme((prev) => ({ ...prev, name: e.target.value }));
            }}
          />
          <label>–§–æ–Ω–æ–≤–æ–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Ç–µ–º—ã</label>
          {theme.backgroundImage ? (
            <div className="img-container">
              <button
                className="del-btn"
                onClick={(e) => deleteThemeImage(e, theme.backgroundImage)}
              >
                ‚ùå
              </button>
              <img
                width="80"
                height="70"
                src={theme.backgroundImage}
                alt="–ò–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Ç–µ–º—ã"
              />
            </div>
          ) : (
            <ChangeableImage
              el={theme}
              type="theme"
              setArray={setArray}
              array={themes}
            />
          )}
        </>
      )}
      <div
        className="sub-container"
        // onDragOver={(e) => dragOverHandler(e)}
        // onDrop={(e) => dropCardHandler(e, theme)}
      >
        {" "}
        {theme.subtopics.length !== 0 ? (
          theme.subtopics.map((sub, index) => (
            <AdminSubItem
              setThemes={setThemes}
              key={sub.id}
              sub={sub}
              themeId={theme.id}
              onDragOver={(e) => dragOverHandler(e)}
              onDragLeave={(e) => dragLeaveHandler(e)}
              onDragStart={(e) => dragStartHandler(e, theme, sub)}
              onDragEnd={(e) => dragEndHandler(e)}
              onDrop={(e) => dropHandler(e, theme, sub)}
              setArray={setArray}
              themes={themes}
            />
          ))
        ) : (
          <span>–¥–æ–±–∞–≤—å—Ç–µ –ø–µ—Ä–≤—ã–π —ç–ª–µ–º–µ–Ω—Ç</span>
        )}
      </div>
    </div>
  );
}

export default AdminThemeItem;
