import React from "react";
import AdminContentItem from "../AdminContentItem/AdminContentItem";
import ChangeableContentContainer from "../ChangeableContentContainer/ChangeableContentContainer";
import ChangeableImage from "../ChangeableImage/ChangeableImage";

function AdminSubItem({
  setThemes,
  sub,
  onDragOver,
  onDragLeave,
  onDragStart,
  onDragEnd,
  onDrop,
  setArray,
  themes,
}) {
  const [isActive, setActive] = React.useState(false);
  const [isChangeable, setChangeable] = React.useState(false);
  const [currentData, setCurrentData] = React.useState(sub);

  function saveChanges() {
    setThemes((themes) =>
      themes.map((theme) =>
        theme.subtopics.includes(sub)
          ? {
              ...theme,
              subtopics: theme.subtopics.map((topic) =>
                topic.id === sub.id ? currentData : topic
              ),
            }
          : theme
      )
    );
    setChangeable((prev) => !prev);
  }

  function undo–°hanges() {
    setChangeable((prev) => !prev);
    setCurrentData(sub);
  }

  function deleteSub() {
    const confirmDel = window.confirm(
      "–í—ã –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å —ç—Ç—É –ø–æ–¥—Ç–µ–º—É?"
    );
    if (confirmDel === true) {
      fetch("/delsub", {
        method: "POST",
        body: JSON.stringify({ sub: sub }),
        headers: {
          "Content-Type": "application/json",
        },
      });
      setThemes((themes) =>
        themes.map((theme) =>
          theme.subtopics.includes(sub)
            ? {
                ...theme,
                subtopics: theme.subtopics.filter(
                  (topic) => topic.id !== sub.id
                ),
              }
            : theme
        )
      );
    }
  }
  function deleteSubImage(e, path) {
    e.preventDefault();
    e.stopPropagation();

    const confirmDel = window.confirm(
      "–í—ã —É–≤–µ—Ä–µ–Ω—ã —á—Ç–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å –∏–∑–æ–±—Ä–∞–¥–µ–Ω–∏–µ? –ü—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ –¥–∞–Ω–Ω—ã–µ —ç—Ç–æ–≥–æ –±–ª–æ–∫–∞ –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ —Å–æ—Ö—Ä–∞–Ω—è—Ç—Å—è"
    );
    if (confirmDel === true) {
      try {
        fetch("/delimage", {
          method: "POST",
          body: JSON.stringify({ img: path }),
          headers: {
            "Content-Type": "application/json",
          },
        })
          .then((response) => response.json())
          .then( async () => {
            try {
              await setCurrentData((prev) => ({ ...prev, img: null }));
              // saveChanges()
            } catch (error) {
              console.log(error);
            }
          })
          .catch((err) => {
            setCurrentData((prev) => ({ ...prev, img: null }));
            setThemes((prev) =>
              prev.map((theme) =>
                theme.subtopics.includes(sub)
                  ? {
                      ...theme,
                      subtopics: theme.subtopics.map((topic) =>
                        topic.id === sub.id ? currentData : topic
                      ),
                    }
                  : theme
              )
            );
          });
      } catch (error) {
        console.error(error);
      }
    }
  }
  function addContent(e) {
    e.preventDefault();
    setCurrentData((prev) => ({
      ...prev,
      content: [
        ...prev.content,
        { img: null, video: null, panorama: null, text: null },
      ],
    }));
  }

  return (
    <>
      {!isChangeable ? (
        <div
          onDragOver={onDragOver}
          onDragLeave={onDragLeave}
          onDragStart={onDragStart}
          onDragEnd={onDragEnd}
          onDrop={onDrop}
          className={!isActive ? "sub-item" : "sub-item active"}
          draggable="true"
        >
          <div className="toolbar">
            <button onClick={() => setActive((prev) => !prev)}>
              {!isActive ? "–ü–æ–¥—Ä–æ–±–Ω–µ–µ" : "–°–≤–µ—Ä–Ω—É—Ç—å"}
            </button>
            <button onClick={() => undo–°hanges()}>–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å</button>
            <button onClick={() => deleteSub()}>–£–¥–∞–ª–∏—Ç—å</button>
          </div>
          <label>–ù–∞–∑–≤–∞–Ω–∏–µ –ø–æ–¥—Ç–µ–º—ã</label>
          <h3>{sub.name}</h3>
          <label>–§–æ–Ω–æ–≤–æ–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –ø–æ–¥—Ç–µ–º—ã</label>
          {sub.img ? (
            <div className="img-container">
              <img
                width="80"
                height="70"
                src={sub.img}
                alt="–ò–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –ø–æ–¥—Ç–µ–º—ã"
              />
            </div>
          ) : (
            <ChangeableImage
              el={sub}
              type="sub"
              setArray={setArray}
              array={themes}
            />
          )}
          <label>–•–µ—à –∫–∞—Ä—Ç—ã –ø–æ–¥—Ç–µ–º—ã</label>
          <p>{sub.map}</p>
          <label>–¢–µ–∫—Å—Ç</label>
          <p>{sub.text}</p>
          <label>–ò–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è –ø–æ–¥—Ç–µ–º—ã</label>
          <div className="content-container" draggable="false">
            {sub.content.map((content) => (
              <AdminContentItem
                key={content.img || content.text || Date.now()}
                content={content}
              />
            ))}
          </div>
        </div>
      ) : (
        <div
          className={"sub-item active"}
          // onClick={() => setActive((prev) => !prev)}
          // draggable="true"
        >
          <div className="toolbar">
            <button onClick={() => saveChanges()}>–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</button>
            <button onClick={() => undo–°hanges()}>–û—Ç–º–µ–Ω–∏—Ç—å</button>
            <button onClick={() => deleteSub()}>–£–¥–∞–ª–∏—Ç—å</button>
          </div>
          <label>–ù–∞–∑–≤–∞–Ω–∏–µ –ø–æ–¥—Ç–µ–º—ã</label>
          <input
            type="text"
            onChange={(e) => {
              setCurrentData((prev) => ({ ...prev, name: e.target.value }));
            }}
            value={currentData.name || ""}
          />
          <label>–§–æ–Ω–æ–≤–æ–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –ø–æ–¥—Ç–µ–º—ã</label>
          {currentData.img ? (
            <div className="img-container">
              <button
                className="del-btn"
                onClick={(e) => deleteSubImage(e, currentData.img)}
              >
                ‚ùå
              </button>
              <img
                width="80"
                height="70"
                src={currentData.img}
                alt="–ò–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –ø–æ–¥—Ç–µ–º—ã"
              />
            </div>
          ) : (
            <ChangeableImage
              el={sub}
              type="sub"
              setArray={setArray}
              array={themes}
            />
          )}
          <label>–•–µ—à –∫–∞—Ä—Ç—ã –ø–æ–¥—Ç–µ–º—ã</label>
          <input
            type="text"
            value={currentData.map || ""}
            onChange={(e) => {
              setCurrentData((prev) => ({ ...prev, map: e.target.value }));
            }}
          />
          <label>–¢–µ–∫—Å—Ç</label>
          <textarea
            type="text"
            value={currentData.text || ""}
            onChange={(e) => {
              setCurrentData((prev) => ({ ...prev, text: e.target.value }));
            }}
          />
          {/* <label>map data</label><p>{sub.map}</p> */}
          <label>–ò–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è –ø–æ–¥—Ç–µ–º—ã</label>
          <button className="add-btn" onClick={(e) => addContent(e)}>
            –î–æ–±–∞–≤–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É
          </button>
          <ChangeableContentContainer
            currentData={currentData}
            setThemes={setThemes}
            sub={sub}
            setCurrentData={setCurrentData}
          />
        </div>
      )}
    </>
  );
}

export default AdminSubItem;
