import React from "react";
import Main from "../Main/Main";
import More from "../More/More";
import Navigation from "../Navigation/Navigation";
import AuthForm from "../AuthForm/AuthForm";

function BaseComp({ isLoading, setLoad, themes, setLogged }) {
  const [activeTheme, setActiveTheme] = React.useState(0);
  const [activePlace, setActivePlace] = React.useState(0);
  const [activePlaceContent, setActivePlaceContent] = React.useState(0);

  React.useEffect(() => {
    (async () => {
      try {
        // const themes = await fetch("/arr").then((data) => data.json());
        const Theme = Math.floor(themes.length / 2);
        const Place = Math.floor(themes[activeTheme].subtopics.length / 2);
        const Content = themes[activeTheme].subtopics[activePlace] !== undefined ? Math.floor(themes[activeTheme].subtopics[activePlace].content.length / 2) : null;
        // setThemes(themes);
        setActiveTheme(Theme);
        setActivePlace(Place);
        setActivePlaceContent(Content);
      } finally {
        setLoad(false);
      }
    })();
  }, []);

  // update states if themes load
  React.useEffect(() => {
    if (themes.length !== 0) {
      return () => {
        setActivePlaceContent(() =>
        themes[activeTheme].subtopics[activePlace] !== undefined ? Math.floor(themes[activeTheme].subtopics[activePlace].content.length / 2) : null
        );
      };
    }
  }, [themes, activeTheme, activePlace]);

  return (
    <>
      <div className="toolbar">
      <Navigation themes={themes} setActiveTheme={setActiveTheme} setActivePlace={setActivePlace} setActivePlaceContent={setActivePlaceContent} setLoad={setLoad}/>
      <AuthForm setLogged={setLogged}/>
      </div>
      <Main
        themes={themes}
        activeTheme={activeTheme}
        setActiveTheme={setActiveTheme}
        activePlace={activePlace}
        setActivePlace={setActivePlace}
        setActivePlaceContent={setActivePlaceContent}
      />
      <More
        themes={themes}
        activeTheme={activeTheme}
        setActiveTheme={setActiveTheme}
        activePlace={activePlace}
        setActivePlace={setActivePlace}
        activePlaceContent={activePlaceContent}
        setActivePlaceContent={setActivePlaceContent}
      />
      <div className="copyright"></div>
    </>
  );
}

export default BaseComp;
