import React from "react";

function AuthForm({ setLogged }) {
  const [isActive, setActive] = React.useState(false)
  async function login(e) {
    e.preventDefault();
    fetch("/login", {
      method: "POST",
      body: JSON.stringify({
        pass: e.target.pass.value,
      }),
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((response) => response.json())
      .then((result) => result === true ? setLogged(true) : null)
      .catch((err) => {
        console.error(err);
      });
  }
  return (
    <React.Fragment>
      <button
        onClick={() => {
          setActive(prev => !prev);
        }}
      >
        <img
          src={!isActive ? "img/profile2.png" : "img/profile.png"}
          alt="acount"
        />
      </button>
      {isActive ? (<form action="" method="post" enctype="multip" onSubmit={(e) => login(e)}>
        <span>–ê–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è</span>
        <input type="password" placeholder="–ü–∞—Ä–æ–ª—å..." name="pass" />
        <input type="submit" value="–í–æ–π—Ç–∏" />
      </form>) : null}
    </React.Fragment>
  );
}

export default AuthForm;
