import React from "react";
import ChangeableImage from "../ChangeableImage/ChangeableImage";

function ChangeableContent({
  content,
  index,
  setCurrentData,
  sub,
  currentContent,
  setCurrentContent,
}) {
  function deleteContent() {
    const confirmDel = window.confirm(
      "–í—ã –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å —ç—Ç—É –∫–∞—Ä—Ç–æ—á–∫—É –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è?"
    );
    if (confirmDel === true)
      setCurrentData((prev) => ({
        ...prev,
        content: prev.content.filter((c, i) => i !== index),
      }));
  }

  function deleteContentImage() {
    setCurrentData((prev) => ({
      ...prev,
      content: prev.content.map((c) =>
        c === content ? { ...content, img: null } : c
      ),
    }));
  }

  function changeContent(e, index) {
    e.preventDefault();
    e.stopPropagation();
    setCurrentData((prev) => ({
      ...prev,
      content: prev.content.map((c, i) =>
        i === index ? { img: c.img, text: e.target.value } : c
      ),
    }));
  }

  function dragOverHandler(e) {
    e.preventDefault();
    e.stopPropagation();
    if (e.target.className === "content-item")
      e.target.style.boxShadow = "0 4px 3px gray";
  }

  function dragLeaveHandler(e) {
    if (e.target.className === "content-item")
      e.target.style.boxShadow = "2px 1px 2px rgb(230, 227, 227)";
  }

  function dragStartHandler(e) {
    e.stopPropagation();

    if (e.target.className === "content-item") {
      setCurrentContent(content);
      console.log(e.target.className);
    }
  }

  function dragEndHandler(e) {
    e.stopPropagation();
    e.preventDefault();
    e.target.style.boxShadow = "2px 1px 2px rgb(230, 227, 227)";
  }

  function dropHandler(e, sub, content) {
    e.stopPropagation();
    e.preventDefault();
    if (e.target.className === "content-item") {
      console.log(content, currentContent);
      e.target.style.boxShadow = "2px 1px 2px rgb(230, 227, 227)";
      setCurrentData((s) => ({
        ...s,
        content: s.content.map((c) => {
          if (c === content) {
            console.log("content === c");
            return currentContent;
          }
          if (c === currentContent) {
            return content;
          }
          return c;
        }),
      }));
    }
  }

  return (
    <div
      className="content-item"
      draggable="true"
      onDragStart={(e) => dragStartHandler(e)}
      onDragLeave={(e) => dragLeaveHandler(e)}
      onDragEnd={(e) => dragEndHandler(e)}
      onDragOver={(e) => dragOverHandler(e)}
      onDrop={(e) => dropHandler(e, sub, content)}
    >
      <div className="tool">
        <button onClick={() => deleteContent()}>‚ùå</button>
      </div>
      {content.img ? (
        <div className="img-container">
          <button className="del-btn" onClick={() => deleteContentImage()}>
            ‚ùå
          </button>
          <img
            width="80"
            height="70"
            src={content.img || null}
            alt="–ò–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ"
          />
        </div>
      ) : (
        <ChangeableImage el={[content, sub]} type="content" />
      )}
      <div className="content-info">
        <input
          key={index}
          value={content.text || ""}
          onChange={(e) => changeContent(e, index)}
          placeholder="Image"
        />
        <input
          key={index}
          value={content.panorama || ""}
          placeholder="Panorama Hash"
          onChange={(e) => changeContent(e, index)}
        />
        <input
          key={index}
          value={content.video || ""}
          placeholder="Video URL"
          onChange={(e) => changeContent(e, index)}
        />
      </div>
    </div>
  );
}

export default ChangeableContent;
