import React from "react";
import Slider from "../Slider/Slider";
import DefLink from "../UI/DefLink";
import ContentInput from "../ContentInput/ContentInput";
function More({
  themes,
  activeTheme,
  activePlace,
  activePlaceContent,
  setActivePlaceContent,
}) {
  return (
    <>
      {themes[activeTheme].subtopics[activePlace] !== undefined ? (
        <section
          className="more"
          id="more"
          style={{
            backgroundImage: `url(${themes[activeTheme].subtopics[activePlace].img})`,
          }}
        >
          <div className="text-content">
            <h2>{themes[activeTheme].subtopics[activePlace].name}</h2>
            <p>{themes[activeTheme].subtopics[activePlace].text}</p>
            <div className="links">
              <DefLink
                href={`https://www.google.com/maps/search/${themes[activeTheme].subtopics[activePlace].name},+–û—à—è–º–Ω—Å–∫–∏–π+—Ä–∞–π–æ–Ω`.replace(
                  / /gi,
                  "+"
                )}
                target="_blank"
              >
                –ü—Ä–æ—Å–º–æ—Ç—Ä–µ—Ç—å –Ω–∞ –∫–∞—Ä—Ç–µ
              </DefLink>
              <DefLink href="#main">–í–µ—Ä–Ω—É—Ç—å—Å—è –∫ —Ç–µ–º–∞–º</DefLink>
            </div>
            <div className="map">
              <iframe
                title="map"
                src={
                  "https://www.google.com/maps/embed?pb=" +
                  themes[activeTheme].subtopics[activePlace].map
                }
                allowfullscreen=""
                loading="lazy"
              ></iframe>
            </div>
          </div>
          <div className="media-content">
            <h3>–ê—Ä—Ö–∏–≤–Ω—ã–µ –º–∞—Ç–µ—Ä–∏–∞–ª—ã</h3>
            <div className="media-input">
              <ContentInput
                themes={themes}
                activeTheme={activeTheme}
                activePlace={activePlace}
                activePlaceContent={activePlaceContent}
              />
            </div>
            <span className="media-input-text">
              {themes[activeTheme].subtopics[activePlace].content[
                activePlaceContent
              ] !== undefined
                ? themes[activeTheme].subtopics[activePlace].content[
                    activePlaceContent
                  ].text
                : null}
            </span>
            <Slider
              activeEl={activePlaceContent}
              setActiveEl={setActivePlaceContent}
              array={themes[activeTheme].subtopics[activePlace].content}
            />
          </div>
        </section>
      ) : null}
    </>
  );
}

export default More;
