import React from "react";

function Navigation({
  themes,
  setActiveTheme,
  setActivePlace,
  setActivePlaceContent,
  setLoad,
}) {
  const [filteredArray, setFilteredArray] = React.useState(themes);
  function search(value = "") {
    if (value !== "") {
      setFilteredArray(
        themes.map((theme) =>
          (theme !== null &&
            theme.name.toLowerCase().indexOf(value.toLowerCase()) !== -1) ===
          true
            ? theme
            : theme.subtopics.some(
                (topic) =>
                  topic.name.toLowerCase().indexOf(value.toLowerCase()) !== -1
              )
            ? {
                ...theme,
                subtopics: theme.subtopics.filter(
                  (sub) =>
                    sub.name.toLowerCase().indexOf(value.toLowerCase()) !== -1
                ),
              }
            : null
        )
      );
    } else {
      setFilteredArray(themes);
    }
  }
  function setTheme(e, index) {
    e.stopPropagation();
    try {
      setActivePlace(Math.floor(themes[index].subtopics.length / 2));
      setActiveTheme(index);
    } finally {
      setLoad(false);
    }
  }
  function setSub(e, indexTheme, indexSub) {
    // e.preventDefault();
    e.stopPropagation();
    // setLoad(true)
    try {
      setActiveTheme(indexTheme);
      setActivePlace(indexSub);
      setActivePlaceContent(
        Math.floor(themes[indexTheme].subtopics[indexSub].content.length / 2)
      );
    } finally {
      setLoad(false);
    }
  }
  return (
    <div className="hamburger-menu">
      <input id="menu__toggle" type="checkbox" />
      <label className="menu__btn" htmlFor="menu__toggle">
        <span></span>
      </label>
      <div className="menu__box">
        <input
          onChange={(e) => search(e.target.value)}
          type="search"
          className="menu__search"
          placeholder="–ü–æ–∏—Å–∫..."
        />
        <div className="box__container">
          <div className="container-items">
            {filteredArray.map((theme, it) => 
                theme !== null ? (
                  <li key={theme.id || Date.now()}>
                    <a
                      className="menu__item"
                      href="#main"
                      onClick={(e) => setTheme(e, it)}
                    >
                      {theme.name}
                    </a>
                    <ul>
                        {theme.subtopics.map((sub, is) => (
                          <li key={sub.id}>
                            <a
                              className="menu__item"
                              href="#more"
                              onClick={(e) => setSub(e, it, is)}
                            >
                              {sub.name}
                            </a>
                          </li>
                        ))}
                      </ul>
                  </li>
                ) : null
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Navigation;
