import React from "react";

function ChangeableImage({el, type, setArray, themes}) {
  function dragStart(e) {
    e.preventDefault();
    e.stopPropagation();
  }
  function dragLeave(e) {
    e.preventDefault();
    e.stopPropagation();
  }
  function onDropHandler(e, el, type) {
    e.preventDefault();
    e.stopPropagation();
    let file = e.dataTransfer.files[0];
    if (file.type === "image/jpeg" || file.type === "image/png" || file.type === "image/avif") {
      const formData = new FormData();
      formData.append("img", file);
      if(type !== 'content') formData.append("element", JSON.stringify(el));
      else {
        formData.append("element", JSON.stringify(el[0]))
        formData.append("parent", JSON.stringify(el[1]))
      }
      formData.append("type", type);
      fetch(
        `/image`,
        {
          method: "POST",
          body: formData,
        }
        
      )
        .then((response) => response.json())
        .then((data) => setArray(data))
        .catch((error) => console.error(error));
    }
    else alert("–î–æ–±–∞–≤—å—Ç–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ jpeg/jpg/png/avif")
  }
  return (
    <div className="img-container">
      <div
        className="drop-area"
        onDragStart={(e) => dragStart(e)}
        onDragLeave={(e) => dragLeave(e)}
        onDragOver={(e) => dragStart(e)}
        onDrop={(e) => onDropHandler(e, el, type)}
        draggable={true}
      >
        –ü–µ—Ä–µ—Ç–∞—â–∏—Ç–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –¥–ª—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è
      </div>
    </div>
  );
}

export default ChangeableImage;
